import React from "react"

class Carousel extends React.Component{
	constructor(props){
		super(props)
		this.state = {
			slides: props.slides,
			index: 0,
			trans: 0,
		}
		this.nextSlide = this.nextSlide.bind(this)
		this.prevSlide = this.prevSlide.bind(this)
			
	}

	nextSlide(){
		this.setState((prevState) => {
			if(prevState.index === prevState.slides.length - 1 ){
				return({index:0, trans:0})
			}else{
				return({index: prevState.index + 1, trans: prevState.trans - this.slideWidth()})
			}
		})
	}
	prevSlide(){
		this.setState((prevState) => {
			if(prevState.index === 0 ){
				return({index:prevState.slides.length - 1, trans: -(this.slideWidth() * (prevState.slides.length - 1))})
			}else{
				return({index: prevState.index - 1, trans: prevState.trans + this.slideWidth()})
			}
		})
	}
	slideWidth = () => {
		return document.querySelector('.slide').clientWidth
	}
	render(){
	  	// const slides = this.state.slides.map((slide) => {
		  // 	const slideStyles = {
			 //    backgroundImage: `url(${slide.img})`,
			 //    backgroundSize: 'cover',
			 //    backgroundRepeat: 'no-repeat',
			 //    backgroundPosition: '50% 60%'
		  // 	}
	  	// 	return <div className="slide" style={slideStyles}></div>
	  	// })
	  	const slides = this.state.slides.map((slide) => {
	  		return(
	  			<div className="slide">
	  				<img src={slide.img} />
	  				<p>{slide.desc}</p>
	  			</div>
	  		)
	  	})

		return(
			<div className="carousel-container">
				<div className="carousel">
					<div className="slide-wrapper" style={{
			            transform: `translateX(${this.state.trans}px)`,
			            transition: 'transform ease-out 0.5s'
	         		}}>
	         		
	         		{slides}
					
					</div>
					<div className="arrow prev-arrow" onClick={this.prevSlide}>
						<img src={require("../Assets/arrow.png")} />
					</div>
					<div className="arrow next-arrow" onClick={this.nextSlide}>
						<img src={require("../Assets/arrow.png")} />
					</div>
					
				</div>
			</div>
		)
	}
}

export default Carousel