import React from "react"
class ParalaxBackground extends React.Component{
	constructor(){
		super()
		this.state = {
			testproject: [
		        {img: "https://s3.us-east-2.amazonaws.com/dzuz14/thumbnails/aurora.jpg", desc: "Image 1"},
		        {img: "https://s3.us-east-2.amazonaws.com/dzuz14/thumbnails/canyon.jpg", desc: "Image 2"},
		        {img: "https://s3.us-east-2.amazonaws.com/dzuz14/thumbnails/city.jpg", desc: "Image 3"},
		        {img: "https://s3.us-east-2.amazonaws.com/dzuz14/thumbnails/desert.jpg", desc: "Image 4"},
		        {img: "https://s3.us-east-2.amazonaws.com/dzuz14/thumbnails/mountains.jpg", desc: "Image 5"},
		        {img: "https://s3.us-east-2.amazonaws.com/dzuz14/thumbnails/redsky.jpg", desc: "Image 6"},
		        {img: "https://s3.us-east-2.amazonaws.com/dzuz14/thumbnails/sandy-shores.jpg", desc: "Image 7"},
		        {img: require("../Assets/attPark.jpg"), desc: "Image 8"}
		    ],
		    tiles: [0,0,0,0,0,0]
		}
		this.setNext = this.setNext.bind(this)
	}
	componentDidMount(){
		this.interval = setInterval(() => {this.setNext()}, 5000)
	}
	componentWillUnmount(){
		clearInterval(this.interval);
	}
	setNext(){
		let randomTile = Math.floor(Math.random() * (this.state.tiles.length - 1))
		let randomImage = Math.floor(Math.random() * (this.state.testproject.length - 1))
		this.setState((prevState) => {
			let newTiles = prevState.tiles
			newTiles[randomTile] = randomImage
			return {tiles: newTiles}
		})
	}
	render(){

		let pictures = this.state.testproject.map((img) => {
			return(
				<div className="image-element-class">
					<img src={img.img} />
				</div>
			)
		})
		let tiles = this.state.tiles.map((next) => {
			return(<Tile image={this.state.testproject[0].img} next={this.state.testproject[next].img}/>)
		})
		return(
			<div className="paralax-background">
				<div className="background-content">
					<div className="tile-box">
						{tiles}
						<div className="abs-button">
							<button onClick={this.setNext}> Next</button>
						</div>
					</div>
				</div>
			</div>
		)
	}
}

class Tile extends React.Component{
	constructor(props){
		super(props)
		this.state = {
			front: props.image,
			back: props.next,
			flipped: false
		}
		this.flipCard = this.flipCard.bind(this)
	}
	async componentDidUpdate(prevProps){
		if(this.props.next !== this.state.front && this.props.next !== this.state.back){
			if(this.state.flipped){
				await this.setState({front: this.props.next})
			}else{
				await this.setState({back: this.props.next})
			}
			this.flipCard()
		}
	}
	flipCard(){
		this.setState((prevState) => {
			return {flipped: !prevState.flipped}
		})
	}
	render(){
		return(
			<div className="tile" onClick={this.flipCard}>
				<div className="flip-card">
					<div id="flip-box" className={this.state.flipped? "flip-card-inner flipped": "flip-card-inner"}>
						<img src={this.state.front} style={{visibility: "hidden"}}/>
						<div className="flip-card-front">
							<img src={this.state.front} />
						</div>
						<div className="flip-card-back">
							<img src={this.state.back} />
						</div>
					</div>
				</div>
			</div>
		)
	}
}
export default ParalaxBackground